import Container from "../Container"
import styled from "styled-components"

export const ContainerOverridden = styled(Container)`

    a, a:visited, a:hover {
        color: ${props => props.textColor};
        text-decoration-color: ${props => props.textColor};
    }

    &.vice {
        background-color: ${props => props.theme.workIntro.background.vice};
        color: ${props => props.theme.workIntro.text.vice};
    }

    &.karma {
        background-color: ${props => props.theme.workIntro.background.karma};
        color: ${props => props.theme.workIntro.text.karma};
    }

    &.cover {
        background-color: ${props => props.theme.workIntro.background.cover};
        color: ${props => props.theme.workIntro.text.cover};
    }

    &.etsy {
        background-color: ${props => props.theme.workIntro.background.etsy};
        color: ${props => props.theme.workIntro.text.etsy};
    }

    &:first-child {
        padding-bottom: calc(var(--su) * -1);
    }

    &:last-child {
        padding-top: calc(var(--su) * 2);
        margin-bottom: calc(var(--su) * 3);

        @media (min-width: 800px) {
            padding-top: var(--su);
            margin-bottom: calc(var(--su) * 2);
        }

    }

`

export const Title = styled.h4`
    margin-bottom: var(--su);

    @media (min-width: 800px) {
        margin-bottom: calc(var(--su) / 2);
    }
    
`

export const Intro = styled.div`

    p {
        margin-bottom: 24px;
    }

`

export const Meta = styled.div`
    div:not(:last-child) {
        margin-bottom: 40px;
    }

    h4:not(:last-child) {
        margin-bottom: 8px;
    }

    h6 {
        margin-bottom: 16px;
    }

`

export const KeyImageWrapper = styled.div`
    margin: 0 0 calc(var(--su) * -4) 0;

    @media (min-width: 800px) {
        margin: 0 0 calc(var(--su) * -2) 0;
    }

`